.Typist .Cursor--blinking {
  opacity: 1;
  -webkit-animation: blink 1s linear infinite;
  animation: blink 1s linear infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

/*
  Fonts setup
*/
@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: normal;
  src: local("Fira Sans"), url("/fonts/fira-sans/FiraSans-Regular.ttf") format("truetype"); }

@font-face {
  font-family: "Fira Sans";
  font-style: italic;
  font-weight: normal;
  src: local("Fira Sans"), url("/fonts/fira-sans/FiraSans-Italic.ttf") format("truetype"); }

@font-face {
  font-family: "Proxima Nova";
  font-style: normal;
  font-weight: bold;
  src: local("Proxima Nova"), url("/fonts/proxima-nova/ProximaNova-Bold.otf") format("opentype"); }

/*
 Defaults
*/
* {
  margin: 0;
  padding: 0;
  outline: none; }

body, html {
  height: 100%;
  width: 100%;
  background-color: #202020;
  color: white;
  font-family: "Fira Sans", sans-serif, arial; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Proxima Nova", sans-serif, arial; }

input {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

/* Reusable classes and shortcuts */
.invert {
  -webkit-filter: brightness(0) invert(1);
  filter: brightness(0) invert(1); }

.clickable {
  cursor: pointer; }

.grabbable {
  cursor: grab; }
  .grabbable:focus, .grabbable:active {
    cursor: grabbing; }

/*
* ===============================================
* BUTTONS
* ===============================================
*/
.btn {
  padding: 5px 10px;
  border-radius: 1px;
  text-decoration: none;
  background-color: transparent;
  border-width: 2px 2px 4px 2px; }

.btn.btn-info {
  color: #00e9cf;
  border-color: #00e9cf; }

.btn.btn-primary {
  color: #f1264c;
  border-color: #f1264c; }

.btn.btn-success {
  color: #00fea5;
  border-color: #00fea5; }

.btn.btn-danger {
  color: #7673bf;
  border-color: #7673bf; }

.btn.btn-warning {
  color: #ffe867;
  border-color: #ffe867; }

.btn.btn-filled {
  color: white; }

.btn.btn-filled.btn-info {
  color: #272727;
  background-color: #00e9cf;
  border: 0px solid #00b6a2;
  border-bottom-width: 4px; }

.btn.btn-filled.btn-primary {
  background-color: #f1264c;
  border: 0px solid #d60e33;
  border-bottom-width: 4px; }

.btn.btn-filled.btn-success {
  color: #272727;
  background-color: #00fea5;
  border: 0px solid #00cb84;
  border-bottom-width: 4px; }

.btn.btn-filled.btn-warning {
  color: #272727;
  background-color: #ffe867;
  border: 0px solid #ffe034;
  border-bottom-width: 4px; }

.btn.btn-filled.btn-danger {
  background-color: #7673bf;
  border: 0px solid #5450af;
  border-bottom-width: 4px; }
